import React, {createRef} from 'react';
import * as d3 from 'd3';
import { BrowserRouter as Router, Switch, Route,Link } from "react-router-dom"
import DashboardPie from './dashboardPie'
import noImage from './../../Assets/img/dashboardpage/1x/noImage1.png'
import arrow from './../../Assets/img/dashboardpage/homepage.png'
import CompareBar from './compareBar'
class Table extends React.Component{
    constructor(props){
  
        super();
        this.ref = createRef();
       
        this.state={
            status:props.data[0].stastistic,
            data:props.data[0].result_new,//fake data
            title:props.data[0].productName
        }
    }
    componentDidMount(){
       
        this.draw(this.state.data);
    }

    draw(data){
        
        
        const svg = d3.select(".dashboard_table");
      
         
        const table = svg.append('table')
        const tbody = table.append('tbody')
        const thead = table.append('thead')
       
        thead.append('tr')
             .selectAll('th')
             .data(Object.keys(data[0]))
             .enter()
             .append('th')
             .text(function(column){
                 return column
             });
       
        var rows = tbody.selectAll('tr')
             .data(data)
             .enter()
             .append('tr');
        
             var cells = rows.selectAll('td')
             .data(function(row){
                 return Object.keys(data[0]).map(function(column){
                    
                     return {column: column, value:row[column]}
                 })
             })
             .enter()
             .append('td')
            //  .each(function(d){
            //      if(d.column == 'Url'){
            //         var self = d3.selectAll(this);
            //         self
            //         // .append("a")
            //         // .attr("href", d.value)
            //         // .text('link')
            //         .append("image")
            //         .attr("x", "0px")
            //         .attr("y", "0px")
            //         .attr('width', "20px")
            //         .attr('height', "20px")
            //         .attr("xlink:href", urlimage)
            //      }else{
            //         var self = d3.select(this);
            //         self
            //         .text(d.value)
            //      }
            //  })
             .html(function(d){
                 
                
                
                
                if(d.column == 'Url'){
                    
                   
                    return "<a target=\"_blank\" href="+ d.value +"><p class=\"displayNone\">External link</p><img alt=\"External link\" src=\"data:image/png;base64,\"/></a>"
                    
                }else if(d.column == 'Price'){
                    
                    
                    return "<p>"+d.value+" US$</p>"
                }else if(d.column == 'Special Offer'){    
                   
                    if(d.value == 'NA'){
                       
                        return "<p class=\"notsale\">"+'Not on sale'+"</p>"
                    }else{
                       
                        return "<p class=\"sale\">"+d.value+" US$</p>"
                    }
                    
                }else if(d.column == 'Product Image'){
                    if(d.value == 'NA'){
                        return "<img style=\"width:150px; height:150px\" alt=\"Product Image\" src =\"" + noImage + "\">"
                    }else{
                        return "<img style=\"width:150px; height:auto\" alt=\"Product Image\" src =\"" + d.value + "\">"
                    }
                    
                }else{
                    return "<p>"+d.value+"</p>"
                }
             })
             
    
       
         return table;
       
    }
    render() {
        return <div>
                    <div className="dashboard_title">
                        <h1>
                            Dashboard<span>{this.state.title} <Link to='/home'><img src={arrow} alt='gohome'/></Link></span>

                        </h1>
                    </div>
                    <div className="dashboard_body">
                        <div className="dashboard_table">
                    
                                <h2>Product Summary (All Products)</h2>
                    
                        </div> 
                        <div className="bottomPieGroup">
                            <DashboardPie data={this.state.status}/>
                            {/* <CompareBar data={this.state.data.slice(0,9)}/> */}
                            <CompareBar data={this.state.data}/>
                        </div>
                       
                    </div>
            </div>
        
        
    }
}

export default Table;